.calendar-container {
    margin: 20px;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    background-color: #fff;
  }
  
  .calendar-table th.day-header {
    text-align: center;
    font-weight: bold;
    padding: 10px;
  }
  
  .calendar-table th.day-header.sunday {
    color: red;
  }
  
  .calendar-table th.day-header.saturday {
    color: blue;
  }
  
  .calendar-table td.calendar-day {
    text-align: center;
    vertical-align: middle;
    padding: 15px;
    height: 50px;
    border: 1px solid #e0e0e0;
    background-color: #f8f9fa;
  }
  
  .calendar-table td.calendar-day.active-day:hover {
    background-color: #d1e7fd;
    cursor: pointer;
  }
  
  .d-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  